  // нора

}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.section {
  padding-top: 16px;
  padding-bottom: 16px;

  @media screen and (min-width: 767.98px) {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: 480px) {
    max-width: 480px;
  }

  @media screen and (min-width: 768px) {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and (min-width: 1280px) {
    max-width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.subtitle {
  font-family: 'Caveat Brush';
  font-size: 24px;
  line-height: calc(30 / 24);
  text-align: center;
  color: var(--primary-text-color);
  font-weight: 400;

  @media screen and (min-width: 1280px) {
    font-size: 36px;
  }
}